import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			pageName:'越限监测点详情',
			radioFlag:0,
			timeID:'',
			typeCode:'',
			lineIndex:'',
			steadyTargetPicUrl:'',
			errorFlag:0,
			typeName:''
		}
	},
	methods: {
		steadyTargetUrl(){
			 var that = this;
			 uni.request({
			 	url: that.serverUrl+'/steadymsg/steadyTargetUrl',
			 	method: 'POST',
			 	data: {
			 		lineIndex: that.lineIndex,
			 		timeID:that.timeID,
					typeCode:that.typeCode
			 	},
			 	header: {
			 		'content-type': 'application/x-www-form-urlencoded'
			 	},
			 	sslVerify: false,
			 	success: (res) => {
			 		uni.hideLoading();
			 		if (res.data.resultCode == '10000') {
			 			that.steadyTargetPicUrl = eval(res.data.data).steadyPath;
			 		} else {
			 			that.errorFlag = 1
			 		}
			 	},
			 	fail: (re) => {
			 		that.errorFlag = 1
			 	}
			 });
		},
		previewPic(url) {
			uni.previewImage({
				urls: [url],
				current:0,
				longPressActions: {
					itemList: ['保存图片'],
					success: function(data) {
						if (data.tapIndex == 0) {
							uni.showLoading({
								title: "图片保存中"
							});
							uni.downloadFile({
								url: url,
								success(result) {
									var tempFilePath = result.tempFilePath;
									uni.saveImageToPhotosAlbum({
										filePath: tempFilePath,
										success() {
											uni.showToast({
												icon: "success",
												title: "保存成功",
												duration: 2000
											})
										}
									})
								},
								complete() {
									uni.hideLoading();
								}
							})
						}
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		retry(){
			this.errorFlag=0;
			this.steadyTargetUrl();
		}
	},
	onLoad(e) {
		this.lineIndex=e.lineIndex;
		this.typeCode=e.typeCode;
		this.timeID=e.timeID;
		this.typeName=e.typeName;
		this.steadyTargetUrl();
	},
	components:{
		back
	}
}
